const vscode = require('vscode');
const simpleGit = require('simple-git')();

function activate(context) {
    let disposable = vscode.commands.registerCommand('extension.openJiraTicket', () => {
        // Obtenez le numéro du ticket à partir de la branche actuelle
        const branchName = vscode.workspace.getWorkspaceFolder(vscode.window.activeTextEditor.document.uri).name;
        const ticketNumber = extractTicketNumberFromBranch(branchName);

        if (ticketNumber) {
            // Construisez l'URL Jira en utilisant le numéro du ticket
            const jiraUrl = `https://your-jira-instance/browse/${getbranchName()}`;

            // Ouvrez l'URL dans le navigateur par défaut
            vscode.env.openExternal(vscode.Uri.parse(jiraUrl));
        } else {

            vscode.window.showErrorMessage(getbranchName());
            vscode.window.showErrorMessage("Aucun numéro de ticket trouvé dans le nom de la branche.");
        }
    });

    const statusBarItem = vscode.window.createStatusBarItem(vscode.StatusBarAlignment.Left, 100);
    statusBarItem.text = "Ouvrir dans Jira";
    statusBarItem.command = 'extension.openJiraTicket';
    statusBarItem.show();

    context.subscriptions.push(statusBarItem, disposable);

}

function extractTicketNumberFromBranch(branchName) {
    return getbranchName()
}

function getbranchName() {
    simpleGit.branch((err, branchSummary) => {
        if (err) {
            console.error('Error:', err);
            return;
        }

        const currentBranch = branchSummary.current;
        console.log('Current branch:', currentBranch);
        return currentBranch;
    })
}

module.exports = {
    activate
};
